.quicklinks-section {
    // Once OK'd can fold into QuadLists, leaving color overrides here
    background: var(--gvgr);
    h2 {
        background: var(--stgr);
        color: var(--white);
        border: 2px solid var(--ssgr);
        border-top-right-radius: 4px;
        border-top-left-radius: 4px;
        padding: 1rem 5% 0.5rem;
        margin-bottom: 0;
        @media screen and (min-width: 780px) {
            text-align: center;
        }
    }
    nav.degrees {
        border: 2px solid var(--stgr);
        border-radius: .25rem;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border-top: 0;
        div {
            &[class*=col-] {
                background: var(--white);
                border-right: 2px solid var(--stgr);
                border-bottom: 2px solid var(--stgr);
                &:last-child {
                    border-right: 0;
                }
                @media screen and (max-width: 768px) {
                    border-right: 0;
                }
                @media screen and (min-width: 768px) {
                    border-bottom: 0;

                }
            } //            &:first-child,
            //            &:nth-child(7),
            //            &:nth-child(13) {
            //                background-color: #55ba47;
            //            }
            //            &:nth-child(6n+2) {
            //                background-color: #c4b581;
            //            }
            //            &:nth-child(6n+3) {
            //                background-color: #004e38;
            //            }
            //            &:nth-child(6n+4) {
            //                background-color: #008554;
            //            }
            //            &:nth-child(6n+5) {
            //                background-color: #d9d0ac;
            //            }
            //            &:nth-child(6n) {
            //                background-color: #00aa4f;
            //            }
            a {
                display: block;
                padding: 2rem;
                background: var(--white);
                color: var(--stgr);
                padding: 1.25em 5% 1.25em 7%;
                text-decoration: none;
                height: 100%;
                line-height: 1;
                font-weight: 400;
                font-size: 1rem;
                @media screen and (min-width: 1140px) {
                    text-align: center;
                    font-size: 1.3rem;
                }
                &:link,
                &:visited {}
                &:hover,
                &:active,
                &:focus {
                    background: var(--wgo20a);
                    color: var(--ssgr);
                    text-decoration: underline;
                }
            }
        }
    }
}
